overflow:auto;
margin-bottom:10px;
.avatar {
    float: left;
    left: 0px;
    .avatar-img {
        width: 50px;
        height: 50px;
        display: block;
        background-position: center center;
        background-size: cover;
        background-color: wheat;
    }
}

.message {
    margin-left: 60px;
    .title {}
    .content-wrap {
        .content {
            p {
                margin: 10px 0 0 0;
                word-break: break-all;
            }
        }
    }
}



